import React from "react";
import { Tabs, Card } from "antd";
import GameList from "../Components/GameList";
import "../Components/GameTabs.css";
import GameArray from "../Components/gameData.json";
import GameCards from "../Components/HomeGameCards";

const { TabPane } = Tabs;
const cricketIcon =
  "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTA8ujXysiZ2Nn15kLBjMEwrsU_k_wLG9GCHQ&usqp=CAU";
const footballIcon =
  "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSvxc0xo1MEXXyEHO_ir9qV5OJe2uTTbwGh5A&usqp=CAU";
const TennishIcon =
  "data:image/png;base64,";
const SoccerIcon = footballIcon;
const BasketballIcon =
  "data:image/png;base64,";
const HorseRaceIcon =
  "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRdJ2bR2Onm02h9Msf_lcsJcLPdqjruFUcOtw&usqp=CAU";
const GameTabs = (props) => {
  return (
    <div className="card-container">
      <div>
        <Tabs theme="dark" type="card game__tabs" centered size="large">
          <TabPane tab="Cricket" key="1">
            <GameList keyword="Cricket" iconUrl={cricketIcon} />
          </TabPane>
          <TabPane tab="Football" key="2">
            <GameList keyword="Soccer" iconUrl={SoccerIcon} />
          </TabPane>
          <TabPane tab="Tennish" key="3">
            <GameList keyword="Soccer" iconUrl={TennishIcon} />
          </TabPane>
          <TabPane tab="Basketball" key="4">
            <GameList keyword="Basketball" iconUrl={BasketballIcon} />
          </TabPane>
          <TabPane tab="Horse Race" key="5">
            <GameList keyword="Horse_Race" iconUrl={HorseRaceIcon} />
          </TabPane>
        </Tabs>
      </div>
      <div>
        <Card title="Live Cashino!!" className="home__cards">
          <div className="home__gameCards">
            {GameArray.map((game, index) =>
              index < 5 ? (
                <GameCards
                  className="home__gameCard"
                  gameTitle={game.gameName}
                  gameUrl={game.img_url}
                  key={index}
                />
              ) : (
                ""
              )
            )}
          </div>
          <div className="home__gameCards">
            {GameArray.map((game, index) =>
              index > 5 && index < 11 ? (
                <GameCards
                  className="home__gameCard"
                  gameTitle={game.gameName}
                  gameUrl={game.img_url}
                  key={index}
                />
              ) : (
                ""
              )
            )}
          </div>
          <div className="home__gameCards">
            {GameArray.map((game, index) =>
              index > 11 && index < 17 ? (
                <GameCards
                  className="home__gameCard"
                  gameTitle={game.gameName}
                  gameUrl={game.img_url}
                  key={index}
                />
              ) : (
                ""
              )
            )}
          </div>
        </Card>
      </div>
    </div>
  );
};
export default GameTabs;
